/**
 * Types for analytics tracking
 */

export type CivicAuthWeb3EventType =
  | "signMessage"
  | "signTransaction"
  | "signTypedData";

export type CivicAuthWeb3ErrorEventType = `${CivicAuthWeb3EventType}Error`;

export type CivicAuthWeb3Event = {
  eventType: CivicAuthWeb3EventType | CivicAuthWeb3ErrorEventType;
  error?: string;
  parameters: readonly unknown[];
  target?: string;
};

export type AnalyticsConfig = {
  endpoint?: string;
  flowId?: string;
  jwt?: string;
};
